<template>
  <div class="gamer-gallery-modal">
    <swiper
      :loop="swiperSettings.loop"
      :navigation="swiperSettings.navigation"
      :modules="modules"
      :thumbs="{ swiper: thumbsSwiper }"
      class="swiper-container gamer-gallery__top"
    >
      <div
        :js-gamer-gallery-modal="`prev-${id}`"
        class="swiper-button-prev swiper-button-custom"
      >
        <icon-chevron-left />
      </div>

      <swiper-slide
        v-for="slide in slides"
        :key="slide.id"
        class="gamer-gallery__cell--top"
      >
        <responsive-image
          :image="{
            src: slide.settings.image
          }"
          min-max="596-596"
          wrapper-class="gamer-gallery__image gamer-gallery__image--top"
          block-class="gamer-gallery-modal"
        />
      </swiper-slide>

      <div
        :js-gamer-gallery-modal="`next-${id}`"
        class="swiper-button-next swiper-button-custom"
      >
        <icon-chevron-right />
      </div>
    </swiper>


    <!-- Thumbnails -->
    <swiper
      :auto-height="false"
      :loop="true"
      :observer="true"
      :observe-parents="true"
      :space-between="16"
      :slides-per-view="'auto'"
      :threshold="10"
      :watch-slides-visibility="true"
      :slide-to-clicked-slide="true"
      :breakpoints="{
        [breakpoints.m]: {
          slidesPerView: slides.length < 5 ? slides.length : 'auto',
          spaceBetween: 16
        }
      }"
      class="swiper-container gamer-gallery__thumbs"
      @swiper="setThumbsSwiper"
    >
      <swiper-slide
        v-for="slide in slides"
        :key="slide.id"
        class="gamer-gallery__cell--thumbnail"
      >
        <responsive-image
          :image="{
            src: slide.settings.image
          }"
          min-max="85-85"
          wrapper-class="gamer-gallery__image gamer-gallery__image--thumbs"
        />
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>

/**
 * Vue: Gamer gallery modal (gamer-gallery-modal)
 * -----------------------------------------------------------------------------
 * Display gamer gallery in a modal.
 *
 */

import { Swiper, SwiperSlide } from 'swiper/vue'
import { Navigation, Thumbs } from 'swiper'

import ResponsiveImage from '~global/images/responsive-image'

import IconChevronRight from '~icons/direction/chevron/right.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'

import breakpoints from '~/config/breakpoints'

export default {
  name: 'GamerGalleryModal',
  components: {
    IconChevronRight,
    IconChevronLeft,
    ResponsiveImage,
    Swiper,
    SwiperSlide,
  },

  props: {
    id: {
      type: String,
      default: '',
      required: true,
    },
    slides: {
      type: Array,
      default: () => ([]),
    },
  },

  setup() {
    return {
      modules: [Navigation, Thumbs],
    }
  },

  data() {
    return {
      breakpoints,
      swiperSettings: {
        autoHeight: false,
        loop: true,
        navigation: {
          nextEl: `[js-gamer-gallery-modal="next-${this.id}"]`,
          prevEl: `[js-gamer-gallery-modal="prev-${this.id}"]`,
        },
        slidesPerView: 'auto',
        threshold: 10,
      },
      thumbsSwiper: null,
    }
  },

  mounted() {
    // Not empty
  },

  methods: {

    setThumbsSwiper(swiper) {
      this.thumbsSwiper = swiper
    },
  },
}
</script>

<style lang="scss">
@import './gamer-gallery-modal';
</style>
